<script>
import { db } from "@/main";
import { auth } from "@/main";
import markdownIt from "markdown-it";
import "katex/dist/katex.min.css";
import mk from "markdown-it-katex";
import mv from "markdown-it-video";
import mf from "markdown-it-footnote";
import sanitizer from "markdown-it-sanitizer";
export default {
  head: {
    title() {
      return {
        inner: ""
      };
    },
    meta: [
      { name: "description", content: "" },
      { property: "og:title", content: "" },
      { property: "og:description", content: "" },
      { property: "og:type", content: "website" },
      { property: "og:url", content: "https://todaiapp.com/" },
      { property: "og:image", content: "../../public/img/logo.png" },
      { name: "twitter:card", content: "summary" }
    ]
  },
  data() {
    return {
      ex1: "# \n## \n### ",
      ex2: "[](https://www.u-tokyo.ac.jp/ja/index.html)",
      ex3: "$x=5$\n\n$$\n5x^2+2\n$$\n\n",
      ex4: "- 1\n- 2\n- 3",
      ex5: "|a  |b  |c  |\n|---|---|---|\n|1  |2  |3  |\n|4  |5  |6  |",
      ex6:
        "![](https://upload.wikimedia.org/wikipedia/commons/2/23/Yasuda_Auditorium_-_Tokyo_University_3.jpg)",
      ex7:
        "\n\n> Lorem ipsum dolor sit amet, purus lectus magna lobortis convallis elit, lobortis volutpat.\n\n\n\nLorem ipsum dolor sit amet, purus lectus magna lobortis convallis elit, lobortis volutpat[^1].\n\n[^1]: Lorem ipsum dolor sit amet, purus lectus magna lobortis convallis elit, lobortis volutpat.",
      currentUser: {},
      title: "",
      content: "",
      answer: "",
      activeTab: "editor",
      selectedSubject: "",
      selectedGenre: "",
      isShown: false,
      genreEnglish: [
        "",
        "",
        "",
        "",
        "",
        "",
        ""
      ],
      genreMath: ["I", "II", "III", "A", "B", "C"],
      md: new markdownIt({
        html: true,
        linkify: true,
        breaks: true,
        typographer: true
      })
        .use(sanitizer)
        .use(mk, {
          throwOnError: false,
          errorColor: " #cc0000"
        })
        .use(mv, { youtube: { width: 250, height: 150 } })
        .use(mf)
    };
  },
  created() {
    auth.onAuthStateChanged(user => {
      this.currentUser = user;
    });
  },
  methods: {
    saveProblem() {
      if (
        this.selectedSubject &&
        this.selectedGenre &&
        this.title &&
        this.content &&
        this.answer
      ) {
        const date = new Date();
        db.collection("problems")
          .add({
            title: this.title,
            subject: this.selectedSubject,
            genre: this.selectedGenre,
            content: this.content,
            user: this.currentUser.uid,
            date: date
          })
          .then(data => {
            this.$toasted.show("Congrats! ", {
              position: "top-center",
              duration: 1500
            }),
              this.createAnswer(data.id);
            this.$router.push("/problem/" + data.id);
          });
      } else {
        this.$toasted.show("", {
          position: "top-center",
          duration: 1500
        });
      }
    },
    createAnswer(id) {
      const date = new Date();
      db.collection("answers")
        .doc(id + "-" + this.currentUser.uid)
        .set({
          content: this.answer,
          problem: id,
          user: this.currentUser.uid,
          date: date,
          problemAuthor: this.currentUser.uid,
          problemTitle: this.title,
          problemSubject: this.selectedSubject,
          problemGenre: this.selectedGenre
        });
    }
  }
};
</script>

